<script>
import axios from "axios";
import getAuthorizationHeader from "../assets/Auth.js";

export default {
  data() {
    return {
      viewIndex: 0,
      hotelIndex: 0,
      restaurantIndex: 0,
      viewdatalist: [],
      activitieslist: [],
      hotellist: [],
      restaurantlist: [],
    };
  },
  mounted() {
    // 取得景點資訊
    axios
      .get(
        "https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot?$filter=Picture%2FPictureUrl1%20ne%20null%20&$format=JSON",
        { headers: getAuthorizationHeader() }
      )
      .then((res) => (this.viewdatalist = res.data))
      .catch((err) => console.log(err));

    // 取得活動資料
    axios
      .get(
        "https://ptx.transportdata.tw/MOTC/v2/Tourism/Activity?$filter=Picture%2FPictureUrl1%20ne%20null%20&$format=JSON",
        { headers: getAuthorizationHeader() }
      )
      .then((res) => (this.activitieslist = res.data))
      .catch((err) => console.log(err));

    // 取得旅宿資料
    axios
      .get(
        "https://ptx.transportdata.tw/MOTC/v2/Tourism/Hotel?$filter=Picture%2FPictureUrl1%20ne%20null%20&$format=JSON",
        { headers: getAuthorizationHeader() }
      )
      .then((res) => (this.hotellist = res.data))
      .catch((err) => console.log(err));

    // 取得餐廳資料
    axios
      .get(
        "https://ptx.transportdata.tw/MOTC/v2/Tourism/Restaurant?$filter=Picture%2FPictureUrl1%20ne%20null%20&$format=JSON",
        { headers: getAuthorizationHeader() }
      )
      .then((res) => (this.restaurantlist = res.data))
      .catch((err) => console.log(err));
  },
  computed: {
    // 景點資料
    showView: function () {

      if (1 == 2) {
        return;
      } else {
        return this.viewdatalist.slice(0, 10 + this.viewIndex);
      }
    },

    // 活動資料
    showActivity: function () {
      return this.activitieslist.slice(0, 3);
    },

    // 旅宿資料
    showHotel: function () {
      return this.hotellist.slice(0, 10 + this.hotelIndex);
    },

    showRestaurant: function () {
      return this.restaurantlist.slice(0, 10 + this.restaurantIndex);
    },
  },
  methods: {
    // 顯示更多景點資訊
    showmoreViews: function () {
      this.viewIndex += 10;
    },

    showmoreHotels: function () {
      this.hotelIndex += 10;
    },

    showmoreRestaurants: function () {
      this.restaurantIndex += 10;
    },
  },
};
</script>