<template>
  <div class="homePagerecommend">
    <ul>
      <li class="recommendList recommendActive" @click="viewRecommend">
        <ion-icon name="sparkles-outline"></ion-icon> 推薦景點
      </li>
      <li class="recommendList" @click="foodsRecommend">
        <ion-icon name="sparkles-outline"></ion-icon> 推薦美食
      </li>
      <li class="recommendList" @click="hotelRecommend">
        <ion-icon name="sparkles-outline"></ion-icon> 推薦旅宿
      </li>
    </ul>

    <div id="viewsItem" class="viewsItem itemsActive">
      <div v-for="(viewdata, index) in recommendView" :key="viewdata.ID">
        <img :src="viewdata.PictureUrl" :alt="viewdata.PictureDescription" />
        <div :id="'viewsItem' + (index + 1)">
          <p>{{ viewdata.Name }}</p>
          <p>
            <ion-icon name="location-outline" class="locationIcon"></ion-icon>
            {{ viewdata.Address }}
          </p>
        </div>
      </div>
    </div>

    <div id="foodsItem" class="foodsItem">
      <div
        v-for="(restaurantdata, index) in recommendRestaurant"
        :key="restaurantdata.ID"
      >
        <img
          :src="restaurantdata.PictureUrl"
          :alt="restaurantdata.PictureDescription"
        />
        <div :id="'foodsItem' + (index + 1)">
          <p>{{ restaurantdata.Name }}</p>
          <p>
            <ion-icon name="location-outline" class="locationIcon"></ion-icon>
            {{ restaurantdata.Address }}
          </p>
        </div>
      </div>
    </div>

    <div id="hotelsItem" class="hotelsItem">
      <div v-for="(hoteldata, index) in recommendhotel" :key="hoteldata.ID">
        <img :src="hoteldata.PictureUrl" :alt="hoteldata.PictureDescription" />
        <div :id="'hotelsItem'+(index+1)">
          <p>{{ hoteldata.Name}}</p>
          <p>
            <ion-icon name="location-outline" class="locationIcon"></ion-icon>
            {{ hoteldata.Address}}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import getAuthorizationHeader from "../../assets/Auth.js";

export default {
  data() {
    return {
      recommendView: [],
      recommendRestaurant: [],
      recommendhotel: [],
    };
  },
  mounted() {
    // 取得景點資料
    axios
      .get(
        "https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot?$filter=Picture%2FPictureUrl1%20ne%20null%20&$format=JSON",
        { headers: getAuthorizationHeader() }
      )
      .then((res) => {
        // 亂數取六筆資料
        {
          for (var i = 0; i < 6; i++) {
            var newdata = {};
            var item = res.data[Math.floor(Math.random() * 500)];
            newdata.PictureUrl = item.Picture.PictureUrl1;
            newdata.PictureDescription = item.Picture.PictureDescription1;
            newdata.Name = item.Name;
            newdata.Address = item.Address.slice(0, 3);
            this.recommendView.push(newdata);
          }
        }
      })
      .catch((err) => console.log(err));

    // 取得餐廳資料
    axios
      .get(
        "https://ptx.transportdata.tw/MOTC/v2/Tourism/Restaurant?$filter=Picture%2FPictureUrl1%20ne%20null%20&$format=JSON",
        { headers: getAuthorizationHeader() }
      )
      .then((res) => {
        // 亂數取六筆資料
        {
          for (var i = 0; i < 6; i++) {
            var newdata = {};
            var item = res.data[Math.floor(Math.random() * 1000)];
            newdata.PictureUrl = item.Picture.PictureUrl1;
            newdata.PictureDescription = item.Picture.PictureDescription1;
            newdata.Name = item.Name;
            newdata.Address = item.Address.slice(0, 3);
            this.recommendRestaurant.push(newdata);
          }
        }
      })
      .catch((err) => console.log(err));

    // 取得旅宿資料
        axios
      .get(
        "https://ptx.transportdata.tw/MOTC/v2/Tourism/Hotel?$filter=Picture%2FPictureUrl1%20ne%20null%20&$format=JSON",
        { headers: getAuthorizationHeader() }
      )
      .then((res) => {
        // 亂數取六筆資料
        {
          for (var i = 0; i < 6; i++) {
            var newdata = {};
            var item = res.data[Math.floor(Math.random() * 1000)];
            newdata.PictureUrl = item.Picture.PictureUrl1;
            newdata.PictureDescription = item.Picture.PictureDescription1;
            newdata.Name = item.Name;
            newdata.Address = item.Address.slice(0, 3);
            this.recommendhotel.push(newdata);
          }
        }
      })
      .catch((err) => console.log(err));

  },
  methods: {
    viewRecommend(e) {
      document.querySelector(".itemsActive").classList.remove("itemsActive");
      document
        .getElementById("viewsItem")
        .classList.add("class", "itemsActive");
      document
        .querySelector(".recommendActive")
        .classList.remove("recommendActive");
      e.target.classList.add("class", "recommendActive");
    },
    foodsRecommend(e) {
      document.querySelector(".itemsActive").classList.remove("itemsActive");
      document
        .getElementById("foodsItem")
        .classList.add("class", "itemsActive");
      document
        .querySelector(".recommendActive")
        .classList.remove("recommendActive");
      e.target.classList.add("class", "recommendActive");
    },
    hotelRecommend(e) {
      document.querySelector(".itemsActive").classList.remove("itemsActive");
      document
        .getElementById("hotelsItem")
        .classList.add("class", "itemsActive");
      document
        .querySelector(".recommendActive")
        .classList.remove("recommendActive");
      e.target.classList.add("class", "recommendActive");
    },
  },
};
</script>

